/**
 * 输入key，换算eos
 **/
import BigNumber from 'bignumber.js';

export const key2EOS = (keyTotal, keyBuy) => {
  // EOS_Spend = (0.0002475 * Supply + 0.00012375 * Key_Buy + 100) * Key_Buy
  const val = new BigNumber(0.0002475).times(keyTotal).div(10);
  const val1 =  new BigNumber(0.00012375).times(keyBuy);
  return val.plus(val1).plus(100).times(keyBuy).div(10000).toNumber()
};

